.init-approve {
  display: flex;
  align-items: center;
  margin-top: 16px;
  justify-content: space-between;
  font-size: 18px;
  .step-approve,
  .step-transaction {
    display: flex;
    align-items: center;
    .finish-text {
      color: #FFF;
    }

    .step-one,
    .step-two {
      margin-right: 10px;
      width: 20px;
      height: 20px;
      background: #203B60;
      display: flex;
      align-items: center;
      font-size: 14px;
      justify-content: center;
      border-radius: 50%;
    }

    .pending {
      margin-right: 10px;
      width: 20px;
      height: 20px;
      background: #3756CD;
      display: flex;
      align-items: center;
      font-size: 14px;
      justify-content: center;
      border-radius: 50%;
    }
  }

  .step-hr {
    width: 88px;
    height: 2px;
    background-color: #1C4986;
  }

  .finish {
    background-color: #3756CD;

  }

}