.sleepContentView {
  width: 12px;
  height: 12px;
  background-image: linear-gradient(#FFF, rgb(67, 95, 208));
  border-radius: 50%;
  text-align: center;
  font-size: 10px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: rotation 1s linear infinite;
}

.sleepContentView .circle {
  width: 10px;
  height: 10px;
  background: #3756CD;
  border-radius: 50%;

}

@keyframes rotation {
  from {
    transform: rotate(0deg);

  }

  to {
    transform: rotate(360deg);

  }

}