
.operate-liquidity {
  height: 410px;
  font-weight: 600;
  position: relative;
  .remove-hint{
    padding-left: 24px;
    padding-top: 10px;
    position: absolute;
    top: 112px;
  }
  .add-input {
    display: flex;
    justify-content: center;
    padding-top: 35px;
  }

  .wallet-balance {
    padding-left: 24px;
    padding-top: 8px;
  }

  .check-proportion {
    width: 332px;
    height: 40px;
    margin: 0 auto;
    margin-top: 32px;
  }

  .information {
    width: 332px;
    height: 72px;
    margin: 0 auto;
    margin-top: 32px;

    .percentage,
    .staked-balance {
      display: flex;
      height: 40px;
      justify-content: space-between;
      font-size: 14px;
      align-items: center;
      .info-box-num{
        color: #E0ECFF;
      }
    }
  }

  .add-remove-btn {
    display: flex;
    justify-content: center;
    padding-top: 19px;
  }
}